@import url('https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@400;700&display=swap');
:root{
  overflow-x: hidden;
}
*{
  margin:0;
  padding: 0;
  box-sizing: border-box;
}
.image-thumbnail{
  width: 6rem;
  height: 6rem;
  border-radius: 20px;
}
.image-thumbnail:not(first-child){
  margin-left: 2rem;
}
.main-thumbnail{
  width: 30rem;
  border-radius: 20px;
  overflow-x: hidden;
}
.main-thumbnail-mobile{
  width: 30rem;
  height: 30rem;
  border-radius: 20px;
  overflow-x: hidden;
  margin-left: -3rem;
}